import Vue from "vue";
import VueRouter from "vue-router";

// 首页
import Home from "../views/Home";

// 商店
import Shop from '../views/Shop'
import ConfirmOrder from '../views/Shop/children/Confirm'
import SuccessOrder from '../views/Shop/children/children/Order'

// 我的
import My from "../views/My";
import Balance from "../views/My/children/Balance";
import BalanceQuery from "../views/My/children/children/BalanceQuery";
import Points from "../views/My/children/Points";
import PointsQuery from "../views/My/children/children/PointsQuery";
import Coupon from "../views/My/children/Coupon.vue";

import Search from "../views/Search";

import ShopCart from "../views/ShopCart";

import Message from "../views/Message";

import Drinks from "../views/Drinks";

import Login from "../views/Login";
// 注册二级页面
import Signin from "../views/Login/children/signin";
Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    redirect: "/home",
    meta: { title: "首页" },
    component: Home,
  },
  {
    path: "/home",
    name: "Home",
    meta: { title: "首页" },
    component: Home,
  },

  // 店铺详情
  {
    path: '/shop',
    name: 'Shop',
    component: Shop,
    meta: { title: '店铺详情' },
    children: [
      {
        path: "confirm",
        name: "Confirm",
        meta: { title: "确认订单", isAuth: true },
        component: ConfirmOrder,
        children: [
          {
            path: "order",
            name: "Order",
            meta: { title: "订单完成", isAuth: true },
            component: SuccessOrder,
          },
        ],
      },
    ],
  },
  {
    path: "/drinks",
    name: "Drinks",
    meta: { title: "饮料甜品" },
    component: Drinks,
  },

  {
    path: "/shopcart",
    name: "ShopCart",
    meta: { title: "订单" },
    component: ShopCart,
  },

  {
    path: "/my",
    name: "My",
    meta: { title: "我的" },
    component: My,
    children: [
      {
        path: "balance",
        name: "Balance",
        meta: { title: "我的余额", isAuth: true },
        component: Balance,
        children: [
          {
            path: "query",
            name: "BalanceQuery",
            meta: { title: "余额说明", isAuth: true },
            component: BalanceQuery,
          },
        ],
      },
      {
        path: "points",
        name: "Points",
        meta: { title: "我的积分", isAuth: true },
        component: Points,
        children: [
          {
            path: "query",
            name: "PointsQuery",
            meta: { title: "积分说明", isAuth: true },
            component: PointsQuery,
          },
        ],
      },
      {
        path: "coupon",
        name: "Coupon",
        meta: { title: "我的优惠券", isAuth: true },
        component: Coupon,
      },
    ],
  },

  {
    path: "/message",
    name: "Message",
    meta: { title: "消息", isAuth: true },
    component: Message,
  },

  {
    path: "/search",
    name: "Search",
    meta: { title: "搜索" },
    component: Search,
  },

  {
    path: "/login",
    name: "Login",
    meta: { title: "登录" },
    component: Login,
    children: [
      {
        path: "signin",
        name: "Signin",
        meta: { title: "注册" },
        component: Signin,
      },
    ],
  },
];

const router = new VueRouter({
  mode: "hash",
  base: process.env.BASE_URL,
  routes,
});

router.beforeEach((to, from, next) => {
  document.title = to.meta.title;

  next();
});

export default router;
